
.constellation-line {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: 0.25;
  -webkit-transition: opacity 250ms;
  transition: opacity 250ms;
}
.active .constellation-line {
  opacity: 1;
}
.navigating-sky .constellation-line {
  opacity: 0;
}



