
.constellation {
  position: absolute;
  pointer-events: none;
}
.constellation:hover .star {
    opacity: 0.66;
}
.constellation .art {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    opacity: 0;
    -webkit-transition: opacity 250ms;
    transition: opacity 250ms;
}
.viewing-sky:not(.navigating-sky) .revealed.active .art,
.viewing-sky:not(.navigating-sky) .revealed:hover .art {
  opacity: 1;
}
.star {
  position: absolute;
  width: 2rem;
  height: 2rem;
  background: white;
  pointer-events: none;
  cursor: pointer;
  opacity: 0;
  -webkit-transition: opacity 250ms;
  transition: opacity 250ms;
}
.star::after {
    content: '';
    width: 25rem;
    height: 25rem;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.viewing-sky .star::after {
  pointer-events: initial;
}



